.interface-wrapper {
  background-color: #000;

}
.interface {
  color: #FFFFFF;
  padding: 11px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  h2 {
    font-size: 30px;
    font-family: RobotoCondensedB, sans-serif;
  }
  .interface-tools {
    display: flex;
    z-index: 1;
  }


  .filter-all,.filter-movies,.filter-tv {
    font-family: RobotoCondensedB, sans-serif;
    width: 135px;
    height: 40px;
    background-color: #000;
    border: 1px solid #fff;
    border-radius: 10px;
    font-size: 21px;
    color: #fff;
    margin-left: 25px;
    cursor: pointer;

  }

  .interface-tools__display {
    align-self: center;
    margin-left: 30px;

    button {
      width: 40px;
      height: 20px;
      margin-left: 20px;
      background-color: transparent;
      border: none;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
  .view-card {
    background-image: url("../img/card-view.svg");
    opacity: 0.4;
  }
  .view-list{
    background-image: url("../img/list-view.svg");
    opacity: 0.4;
  }


  .active-view1, .active-view2{
    opacity: 1;
  }

  .active-filter1, .active-filter2 {
    background-color: #fff;
    color: #000;
  }
}